import React, { Fragment } from "react";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { AccessControlTypes, Dashboard, Feature, Framework } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";
import { useQuery } from "react-query3";

export default function DashboardModal() {
  const { licensed } = useAppStore();
  const { onOk, form } = useModalContext();
  const { data } = useQuery<Framework>("/dashboardframework/newest");

  function handleOk() {
    onOk("/dashboard", "create", {
      dashboardFramework: { id: 0 },
    } as Dashboard);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Dashboard" />
      </ModalOpenButton>
      <ModalContent title="Create new dashboard" onClickOk={handleOk} formName="create_dashboard_form">
        <Form schemaName="dashboard">
          <FormContentBase
            name="create_dashboard_form"
            form={form}
            preserve={false}
            defaultValues={{
              view: 'new',
              baseUrl: "/dashboard",
              authenticated: licensed(Feature.Dashboard),
              dashboardFramework: data?.id,
              gotoDashboard: true,
              autoStart: true,
              autoDeploy: false,
              environment: "Default",
              role: [],
              template: "Blank.ps1"
            }}
          />
          {!licensed(Feature.Dashboard) && <BuyLicenseAlertBox />}
        </Form>
      </ModalContent>
    </Fragment>
  );
}
